
import { defineComponent } from 'vue'

import { reactiveFetcher } from '@/api/fetch'
import { graphqlFetch } from '@/api/graphql-api/GraphqlApi'
import { GET_SWIPES } from '@/api/graphql-api/queries/swipeQueries'
import { SwipeType } from '@/types/swipe'
import HomeSale from '@/views/PublicLayout/home/HomeComponents/HomeContent/HomeSale/HomeSale.vue'
import HomeSwiper from '@/views/PublicLayout/home/HomeComponents/HomeContent/HomeSwiper/HomeSwiper.vue'
import HomeSwiperSkeleton from '@/views/PublicLayout/home/HomeComponents/HomeContent/HomeSwiper/HomeSwiperSkeleton.vue'

export default defineComponent({
  name: 'HomeContent',
  components: {
    HomeSale,
    HomeSwiper,
    HomeSwiperSkeleton,
  },
  setup() {
    const { data: swiperData, loading: swiperLoading } = reactiveFetcher<
      SwipeType[]
    >('NF', '/home-content-swiper', () => graphqlFetch(GET_SWIPES))
    return { swiperData, swiperLoading }
  },
})
